<template>
<div>
	<div class="tableSelect">
		<!-- <select id="required" @change="">
			<option value="1" disabled selected>()</option>
			<option value="2"></option>
			<option value="3"></option>
		</select> -->
		<select v-model="selGradeType">
			<option disabled></option>
			<option v-for="gradeTypeOne in gradeType" :value="gradeTypeOne.text">{{gradeTypeOne.text}}</option>
		</select>
		<select v-model="selExecSemster">
			<option disabled></option>
			<option v-for="execSemsterOne in execSemster" :value="execSemsterOne.text">{{execSemsterOne.text}}</option>
		</select>
		<select v-model="selCourseName">
			<option disabled></option>
			<option v-for="courseNameOne in courseName" :value="courseNameOne.text">{{courseNameOne.text}}</option>
		</select>
		<select v-model="selClassId">
			<option disabled></option>
			<option v-for="classIdOne in classId" :value="classIdOne.text">{{classIdOne.text}}</option>
		</select>
		<span class="inputNumber">
			<input v-model="studentId" placeholder="">
		</span>
		<button class="am-btn am-btn-success am-radius" @click="inquireBtn()"></button>
		<button class="am-btn am-btn-success am-radius" @click="exportBtn()"></button>
	</div>

	<div id="gradeContent">
		<div class="gradeContentBody">
			<table class="normalTable">
				<thead>
					<tr>
						<th></th>
						<th></th>
						<th></th>
						<th></th>
						<th></th>
						<th></th>
						<th></th>
					</tr>
				</thead>
				<tbody>
					<tr v-for="data in scoreListByStuNo">
						<td v-text="data.studentId"></td>
						<td v-text="data.studentName"></td>
						<td v-text="data.term"></td>
						<td v-text="data.specialityName"></td>
						<td v-text="data.stuSemester"></td>
						<td v-text="data.courseName"></td>
						<td v-text="data.grade"></td>
					</tr>
				</tbody>
			</table>
		</div>
	</div>
</div>
</template>

<script>
export default {
	name: 'gradeContent',
	data () {
		return {
			selGradeType: '',
			selExecSemster: '',
			selCourseName: '',
			selClassId: '',
			gradeType: [],
			execSemster: [],
			courseName: [],
			classId: [],
			studentId: '',
			// 
			scoreListByStuNo: [
				// {stuNum: '20142201010', stuName: '', stuGrade: '', stuMajor: '', stuSemester: '2016-2017', stuCourse: '', stuScore: '80'}
				{},{},{}
			]
		}
	},
	beforeMount: function() {
        this.$http.post('./findScoreByStuNo',{},{
            "Content-Type":"application/json"
        }).then(function(response){
            console.log(":");
            console.log(response.body);
            var data = response.body;
            this.gradeType = data.gradeType;
            this.execSemster = data.execSemster;
            this.courseName = data.courseName;
            this.classId = data.classId;
        },function(error){
            console.log("error:");
            console.log(error);
        });
    },
    methods: {
    	inquireBtn: function() {
    		this.$http.post('./findScoreByStuNo',{
	        	"gradeType": this.selGradeType,
	        	"execSemster": this.selExecSemster,
	        	"courseName": this.selCourseName,
	        	"classId": this.selClassId,
	        	"studentId": this.studentId
	        },{    
	            "Content-Type":"application/json"
	        }).then(function(response){
	            console.log(":");
	            console.log(response.body);
	            var data = response.body;
	            if (data.result == "1") {
	            	this.scoreListByStuNo = data.scoreListByStuNo;
	            }else{
			        alert("");
			    }
	        },function(error){
	            console.log("error:");
	            console.log(error);
        	});
    	},
    	exportBtn: function() {

    	}
    }
}
</script>

<style>
#gradeContent {
	background-color: #f3f3f3;
	width: 100%;
	text-align: center;
}
.gradeContentBody {
	padding: 1rem 5rem;
}
/*.gradeContentBody table {
	margin-bottom: 1.6rem;
}*/

</style>



