<template>
<div>	
	<div class="tableSelect">
		<select v-model="selGradeType">
			<option disabled></option>
			<option v-for="gradeTypeOne in gradeType" :value="gradeTypeOne.text">{{gradeTypeOne.text}}</option>
		</select>
		<select v-model="selExecSemster">
			<option disabled></option>
			<option v-for="execSemsterOne in execSemster" :value="execSemsterOne.text">{{execSemsterOne.text}}</option>
		</select>
		<select v-model="selSpecialityName">
			<option disabled></option>
			<option v-for="specialityNameOne in specialityName" :value="specialityNameOne.text">{{specialityNameOne.text}}</option>
		</select>
		<select v-model="selCourseName">
			<option disabled></option>
			<option v-for="courseNameOne in courseName" :value="courseNameOne.text">{{courseNameOne.text}}</option>
		</select>
        <span class="inputFraction">
	        <input v-model="minScore" class="inputGrade">  
	       	<input v-model="maxScore" class="inputGrade">
	    </span>
        <button class="am-btn am-btn-success am-radius" @click="inquireBtn()"></button>
		<button class="am-btn am-btn-success am-radius" v-on:click="exportBtn()"></button>
	</div>

	<div id="gradeTable">
		<div class="gradeTableBody">
			<table class="normalTable">
				<thead>
					<tr>
						<th></th>
						<th></th>
						<th></th>
						<th></th>
						<th></th>
						<th></th>
						<th></th>
					</tr>
				</thead>
				<tbody>
					<tr v-for="data in scoreList">
						<td v-text="data.studentId"></td>
						<td v-text="data.studentName"></td>
						<td v-text="data.term"></td>
						<td v-text="data.specialityName"></td>
						<td v-text="data.stuSemester"></td>
						<td v-text="data.courseName"></td>
						<td v-text="data.grade"></td>
					</tr>
				</tbody>
			</table>
		</div>
	</div>
</div>
</template>

<script>
export default {
	name: 'gradeTable',
	data () {
		return {
			selGradeType: '',
			selExecSemster: '',
			selSpecialityName: '',
			selCourseName: '',
			gradeType: [],
			execSemster: [],
			specialityName: [],
			courseName: [],
			minScore: '',
			maxScore: '',
			// 
			scoreList: [
				// {stuNum: '20142201010', stuName: '', stuGrade: '', stuMajor: '', stuSemester: '2016-2017', stuCourse: '', stuScore: '80'}
				{},{},{}
			]
		}
	},
	beforeMount: function() {
        this.$http.post('./findScore',{},{
            "Content-Type":"application/json"
        }).then(function(response){
            console.log(":");
            console.log(response.body);
            var data = response.body;
            this.gradeType = data.gradeType;
            this.execSemster = data.execSemster;
            this.specialityName = data.specialityName;
            this.courseName = data.courseName;
        },function(error){
            console.log("error:");
            console.log(error);
        });
    },
	methods: {
		inquireBtn: function() {
    		this.$http.post('./findScore',{
	        	"gradeType": this.selGradeType,
	        	"execSemster": this.selExecSemster,
	        	"specialityName": this.selSpecialityName,
	        	"courseName": this.selCourseName,
	        	"minScore": this.minScore,
	        	"maxScore": this.maxScore
	        },{    
	            "Content-Type":"application/json"
	        }).then(function(response){
	            console.log(":");
	            console.log(response.body);
	            var data = response.body;
	            if (data.result == "1") {
	            	this.scoreList = data.scoreList;
	            }else{
			        alert("");
			    }
	        },function(error){
	            console.log("error:");
	            console.log(error);
        	});
    	},
		exportBtn: function () {

  		}
	}
}
</script>

<style>
#gradeTable {
	background-color: #f3f3f3;
	width: 100%;
	text-align: center;
}
.gradeTableBody {
	padding: 1rem 5rem;
}
.gradeTableBody select {
	margin: 0 1rem;
}
/*.gradeTableBody table {
	margin-bottom: 1.6rem;
}*/

</style>



