<template>
<div>
	<div class="blank">
		<div class="leftPart">
			<span>{{preSemester}}</span>
			<span>
				<span class="textBlue">{{preWeek}}</span>
			</span>
		</div>
		<div class="rightPart">
			<span></span>
			<span class="textBlue">15</span>
		</div>
	</div>

	<div id="tchGrdPutBody">
		<div class="tchGrdPutDiv">
			<!-- <span>2016-20174</span> -->
			<div class="tchGpMain">
				<div class="tchGrdTable">
					<table class="operationTable">
						<thead>
							<tr>
								<th width="7%"></th>
								<th width="5%"></th>
								<th width="12%"></th>
								<th width="11%"></th>
								<th width="34%"></th>
								<th width="10%"></th>
								<th width="9%"></th>
							</tr>
						</thead>				
						<tbody>
							<tr v-for="data in info" >
								<td class="textBtn"><a @click="gradeInputBtn"></a></td>
								<td v-text="index + 1"></td>
								<td v-text="data.semester"></td>
								<td v-text="data.tchName"></td>
								<td v-text="data.className"></td>
								<td v-text="data.courseName"></td>
								<td class="textBtn"><a @click="exportFormatBtn"></a></td>
							</tr>
						</tbody>
					</table>
				</div>
			</div>
		</div>
	</div>
</div>
</template>

<script>
export default {
	name: 'tchGrdPutBody',
	data () {
		return {
			preSemester: '2016-2017',
			preWeek: '20',
			// warmPrompt: '15',
			info: [
				{semester: '2016-2017', tchName: '61742', className: '20159+31+20152A', courseName: ''}
			]
		}
	},
	beforeMount: function() {
        this.$http.post('../tchGradesInput.php',{},{
            "Content-Type":"application/json"
        }).then(function(response){
            console.log(":");
            console.log(response.body);
            var data = response.body;
            this.preSemester = data.preSemester;
			this.preWeek = data.preWeek;
			this.info = data.info;
        },function(error){
            console.log("error:");
            console.log(error);
        });
    },
	methods: {
		// 
		gradeInputBtn: function () {
			window.location.href = "/tchName/gradesInput";
		},
		exportFormatBtn: function () {
			var r = confirm("");
			if (r==true) {
				alert("");
			}
			else {
				alert("");
			}
		}
	}
}
</script>

<style>
#tchGrdPutBody {
	background-color: #f3f3f3;
}
.tchGrdPutDiv {
	padding: 0.6rem 0;
	text-align: center;
	font-size: 1rem;
}
.tchGpMain {
	text-align: center;
	background-color: white;
	margin: 0.5rem 5rem;
	padding-bottom: 1.3rem;
	border: 1px solid #ececec;	/**/
}
.tchGrdTable {
	margin: 0.5rem 0.5rem;
	/*height: 20rem;*/
	border: 0.7rem solid transparent;
}
.tchGrdTable table {
	border-collapse: collapse;
	/*height: 3.2rem;*/
}

/*.textBtn a {
	text-decoration: underline;
	color: #3399ff;
	cursor: pointer;
}
.textBtn a:hover,
.textBtn a:focus {
	color: #5cadff;
}
.textBtn a:active {
	color: #3091f2;
}*/
</style>


